$main-green: #17aa66;
$greyish-brown: #555;
$main-green-dark: #186141;
$white: #fff;
$dark-grey: #333;
$warm-grey: #777;
$greyish: #aaa;
$light-grey: #ccc;
$lighter-grey: #ddd;
$lightest-grey: #eee;
$warmer-grey: #888;
$bottom-grey: #e5e5e5;

$weight-default: 400;
$weight-semibold: 600;
$weight-bold: 700;

html {
	height: 100%; }

body {
	background: $white;
	font: 400 15px/1 "proxima-nova", "Proxima Nova", "proxima-nova-alt", "Proxima Nova", sans-serif;
	color: $greyish-brown;
	-webkit-font-smoothing: antialiased;
	box-sizing: border-box; }


.clear {
	clear: both;
	display: block;
	overflow: hidden;
	visibility: hidden;
	width: 0;
	height: 0;

	&:after {
		clear: both;
		display: block;
		overflow: hidden;
		visibility: hidden;
		width: 0;
		height: 0; } }

p {
	line-height: 1.5; }

a {
	color: $dark-grey;
	text-decoration: none;

	&:hover {
		text-decoration: underline; } }

.logo, .icon {
	display: inline-block; }

div.logo, div.icon {
	display: block; }

.toggle-mobile-nav {
	display: inline-block; }

.hamburger {
	display: inline-block;
	width: 50px;
	height: 38px;
	padding: 11px 10px 10px;

	.meat {
		background: $dark-grey;
		height: 3px;
		margin-bottom: 4px;
		display: block; }

	&.hamburger--white .meat {
		background: $white; }

	.meat.meat--last {
		margin-bottom: 0; } }

header {
	height: 119px;
	background-color: $dark-grey;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;

	&.header--with-bg {
		background-image: url(/images/header_bg_small.jpg); }

	&.header--with-large-bg {
		background-image: url(/images/header_bg.jpg); }

	.header_top {
		padding: 39px 35px; }

	.header_top_tb {
		padding: 39px 0; } }

nav[role="navigation"] {
	float: right;

	ul {
		list-style: none;
		margin: 0;
		padding: 0;

		li {
			display: inline-block;

			a {
				text-transform: uppercase;
				color: $white;
				font-size: 16px;
				line-height: 20px;
				padding: 10px 15px;
				text-decoration: none;
				display: inline-block;
				border-bottom: 1px solid transparent;

				&:hover {
					color: $white;
					text-decoration: none;
					border-bottom: 1px solid $white; } } } } }

main {}

footer {
	.main {
		padding: 40px 0;
		background: $lightest-grey;

		h3.footer_title {
			font-weight: $weight-semibold;
			font-size: 16px;
			line-height: 1.5;
			color: $greyish-brown;
			margin: 0 0 20px; }

		p, address {
			font-size: 14px;
			line-height: 1.4;
			color: $warm-grey;
			margin: 0; } }

	.bottom {
		padding: 25px 0;
		background: $bottom-grey;

		.footer_bottom_copy {
			font-weight: $weight-semibold;
			color: $warmer-grey;
			margin-right: 20px;
			font-size: 14px; }

		ul.footer_bottom_links {
			> li {
				display: inline-block;
				margin-right: 30px;

				&:last-child {
					margin-right: 0; }

				a {
					color: $greyish-brown;
					font-size: 14px;
					font-weight: $weight-semibold; } } } } }

@media all and (max-width: 767px) {
	header {
		height: 79px;

		.header_top, .header_top_tb {
			padding: 21px 0 19px; } } }
